<template>
    <div>
        <div class="hero-body">
            <section class="container">
            <h1 class="title">Portfolio</h1>            
            <h2 class="subtitle">My Latest Works</h2>
            <div class="field is-grouped is-grouped-multiline">
                <div class="control">
                    <span class="tag cursor--hand" :class="{'is-info':undefined == selected}" @click="selected = undefined">All</span>                    
                </div>
                <div class="control" v-for="cat in cats" :key="cat.id">
                    <span class="tag cursor--hand" :class="{'is-info':cat.id == selected}" @click="selected = cat.id">{{ cat.fields.judul }}</span>
                </div>
            </div>
            <br>
            <div class="columns" v-for="portos in chunckPorto" :key="portos.id">
                <div class="column is-one-third" v-for="porto in portos" :key="porto.id" v-show="filterByKategori(porto)">
                    <div class="card" :class="getListKategoriByClass(porto)">
                        <div class="card-image">
                            <figure class="image is-4by3">
                                <img style="object-fit:cover !important" :src="porto.fields.gambar.split(', ')[0]" :alt="porto.fields.judul">
                            </figure>
                        </div>
                        <div class="card-content">
                            <strong class="title is-5">
                                <router-link class="has-text-black" :to="`/portfolio/detail/${porto.fields.slug}`">{{porto.fields.judul}}</router-link>
                            </strong>
                            <p>{{ porto.fields.desc.substring(0, 100) }}...</p><br>
                            <small><b>Built with :</b> {{ porto.fields.tag }}</small><br><br>
                            <router-link class="button is-dark" :to="`/portfolio/detail/${porto.fields.slug}`">View</router-link>
                        </div>
                    </div>
                </div> 
            </div>
        </section>
        </div>        
    </div>
</template>

<script>
import { AirtableAPI } from '../services/airtable-api';
const airAPI = new AirtableAPI();
let chunk = require('chunk');

export default {
    name: "ProjectList",
    props: ['limit'],
    data() {
        return {
            selected: undefined,
            portos: [],
            cats: [],
            idcats: []
        }
    },
    methods: {
        async getData() {
            await airAPI.getAll('Portofolio_Kategori').then((data) => {
                this.cats = data;
                data.forEach((r) => {
                    this.idcats[r.id] = r;
                });
            });
            await airAPI.getAll('Portofolio', this.limit).then((data) => {
                data.forEach((r) => {
                    r.fields.gambar = r.fields.gambar.replace(/\\/g, "")
                    
                    // for kategori
                    r.fields.kategori.forEach((kat, idx) => {
                        r.fields.kategori[idx] = new Object();
                        r.fields.kategori[idx].id = kat;
                        r.fields.kategori[idx].nama = this.idcats[kat].fields.judul;
                        r.fields.kategori[idx].class = this.idcats[kat].fields.judul.replace(' ', '_');
                    });
                }) 
                this.portos = data;
            })
        },

        clickFilter: function() {
            this.isActive = !this.isActive;
        },

        filterByKategori(porto) {
            return this.selected == undefined || this.getListKategoriById(porto).includes(this.selected)
        },

        getListKategoriByClass(item) {
            return item.fields.kategori.map(cat => cat.class);
        },

        getListKategoriById(item) {
            let id = []
            item.fields.kategori.forEach((r) => {
                id.push(r.id);
            });
            return id;
        }
    },
    computed: {
        chunckPorto() {
            return chunk(this.portos, 3)
        }
    },
    mounted() {
        this.getData();
    }
}
</script>
