import { AirtableAPI } from '../services/airtable-api';
const airAPI = new AirtableAPI();

export async function quoteNow() {
    let quote = [];

    await airAPI.getAll('Quote').then((data) => {
        data.forEach((r) => {
            quote.push(r.fields.judul);
        });
    });
    return quote[Math.floor((Math.random()*quote.length))];
}